<template>
  <div id="app">
    <b-card title="Mcc Http Sender"
            sub-title="Copyright Neonexsoft.inc">
        <p class="card-text">
            This is a MC Mockup Page for sending a message from the Marine Cloud to the Marine Cloud Connector.
        </p>
        <b-input-group>
          <b-input-group-addon>
            <b-form-select v-model="selectedmethod" :options="options" style="width:200px">
            </b-form-select>
          </b-input-group-addon>
          <!-- Main form input -->
          <b-form-input
            id="targetUrl"
            type="text"
            v-model.trim="targeturl"
            :state="urlState"
            aria-describedby="targetUrlFeeback"
            placeholder="Enter URL"></b-form-input>
          <b-btn variant="info" @click="sendToMc"> SEND </b-btn>
        </b-input-group>
        <b-form-invalid-feedback  style="display:block">
          {{targetUrlFeeback}}
        </b-form-invalid-feedback>
    </b-card> 
    <b-container fluid class="mt-3">
      <b-row>
        <b-col>
          <b-card no-body>
            <b-tabs pills card vertical>
              <b-tab title="Request" active>
                <b-container fluid class="bv-example-row">
                  <b-row v-for="(input, index) in inputs" :key="input.id"  class="mt-2">
                     <b-input-group>
                      <b-form-input type="text" id="headerKey" placeholder="New Key" size="sm" v-model="input.name"></b-form-input>
                      <b-form-input type="text" id="headerValue" placeholder="Value" size="sm" v-model="input.value"></b-form-input>
                      <b-btn size="sm" variant="secondary" @click="deleteRow(index)"> X </b-btn>
                      <!-- Main form input -->
                    </b-input-group>
                  </b-row>
                  <b-row class="mt-2">
                      <b-col><b-button variant="success" @click="addRow" >Add Header (name : value) Row</b-button></b-col>
                  </b-row>
                  <b-row class="mt-2">
                      <b-col>
                        <b-form-select v-model="selectedsvlist" :options="svlist" :state="getSVFile">
                        </b-form-select>
                      </b-col>
                  </b-row>
                  <b-row class="mt-2">
                      <b-col> <b-form-textarea v-model="payload" placeholder="Enter something" :rows="20" :max-rows="30" contenteditable></b-form-textarea></b-col>
                  </b-row>
                </b-container>
              </b-tab>
            </b-tabs>
          </b-card>
        </b-col>
        <b-col>
          <b-container fluid>
            <b-row>
              <b-col>
                <b-card no-body>
                  <b-tabs pills card vertical>
                    <b-tab title="Mcc Response" active>
                      <h4>status :{{responseStatus}}</h4>
                      <pre>{{responseHeader}}</pre>
                      <b-form-textarea v-model="responseData" placeholder="Response Body" :rows="12" :max-rows="12" readonly class='responseBodyForm'></b-form-textarea>
                    </b-tab>
                  </b-tabs>
                </b-card>
              </b-col>
            </b-row>
            <b-row class="mt-2">
              <b-col>
                <b-card no-body>
                  <b-tabs pills card vertical>
                    <b-tab title="Mc Response" active>
                      <h4>status :</h4>
                      <pre></pre>
                      <b-form-textarea placeholder="Response Body" :rows="14" :max-rows="14" readonly class='responseBodyForm'></b-form-textarea>
                    </b-tab>
                  </b-tabs>
                </b-card>
              </b-col>
            </b-row>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
    <!--div>selectedmethod: <strong>{{ selectedmethod }}</strong></div-->
  </div>
</template>

<script>
export default {
  data () {
    return {
      targetUrlFeeback: '',
      responseStatus: '',
      responseStatusText: '',
      responseHeader: '',
      responseData: '',
      targeturl: 'http://143.248.55.83:8088',
      payload: '',
      inputs: [
        { name: 'dstMRN', value: 'urn:mrn:smart:service:instance:mof:S40T' },
        { name: 'srcMRN', value: 'urn:mrn:mcl:vessel:dma:poul-lowenorn' }
      ],
      selectedmethod: 'GET',
      options: [
        { value: 'GET', text: 'GET' },
        { value: 'POST', text: 'POST' },
        { value: 'PUT', text: 'PUT', disabled: true },
        { value: 'PATCH', text: 'PATCH', disabled: true },
        { value: 'DELETE', text: 'DELETE', disabled: true },
        { value: 'OPTIONS', text: 'OPTIONS', disabled: true }
      ],
      selectedsvlist: '',
      svlist: [
        { value: '', text: 'CHOOSE E-NAVI SV XXX SERVICE' }
      ]
    }
  },
  created: function () {
    this.$http.post('http://localhost:7090/svlist')
    .then((response) => {
      response.data.forEach(element => {
        this.svlist.push(element)
      })
    })
    .catch(function (error) {
      console.log(error)
    })
  },
  methods: {
    addRow () {
      this.inputs.push({ name: this.inputs.index })
    },
    deleteRow (index) {
      this.inputs.splice(index, 1)
    },
    sendToMc () {
      this.$http.post('http://localhost:7090/path', {
        targetUrl: this.targeturl,
        method: this.selectedmethod,
        headers: this.inputs,
        body: this.payload
      })
      .then((response) => {
        this.responseStatus = response.data.responseCode
        this.responseHeader = response.data.headers
        this.responseData = response.data.body
      })
      .catch(function (error) {
        console.log(error)
      })
    }
  },
  computed: {
    urlState () {
      if (this.targeturl.length < 2) {
        this.targetUrlFeeback = 'Enter at least 3 letters'
      } else {
        this.targetUrlFeeback = ''
        return true
      }
      return false
    },
    getSVFile () {
      if (this.selectedsvlist !== '') {
        this.$http.post('http://localhost:7090/svfile', {
          fileName: this.selectedsvlist
        })
        .then((response) => {
          this.payload = response.data
        })
        .catch(function (error) {
          console.log(error)
        })
      }
    }
  }
}
</script>
          
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.responseBodyForm {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 12px;
}

</style>



